import * as React from "react";
import styles from "./Footer.module.css"

const PaymentModalContent = () => {
    return(
        <>
            <div className={styles["modalContent"]}>
                {
                    "           ,    -."
                    + "\n\n"
                    + "        ,   -."
                    + "\n\n"
                    + " 22:00     -!\n"
                    + " 8:00     ."
                }
            </div>
        </>
    )
}

export default PaymentModalContent